import React from 'react';
import { useForm } from 'react-hook-form';
import { toast } from 'react-toastify';
import * as s from './styles';
import api from '../../services/api';
import {isEmpty} from '../../util/functions/isEmpty';


function Form() {
  const { register, handleSubmit, errors } = useForm();

  function resetInput() {
    const inputAll = document.querySelectorAll('input').forEach((input) => {
      input.value = '';
    });
    return inputAll;
  }

  const upData = async ({
    name,
    userName,
    password,
    passwordConfirm,
  }) => {
    if (password !== passwordConfirm) {
      toast.error('senhas não correspondentes');
    } else {
      try {
        const newData = {
          name,
          userName,
          password,
        };
        // const newDataToJson = JSON.stringify(newData);
        const {data} = await api.post('/client/create', newData);
        const {usuario, message} = data;
       
        if(!isEmpty(usuario)){
          resetInput();
          toast.success(message);
        } else {
          toast.erro('os dados não puderam ser salvos :/');
        }
      } catch (err) {
        toast.error(`erro ao enviar os dados ${err}`);
      }
    }
  };

  return (
    <s.Container>
      <h1>Cadastro de usuário</h1>
      <form onSubmit={handleSubmit(upData)}>
        <span className="span1">Nome</span>
        <input
          type="text"
          name="name"
          ref={register({ required: 'preencha o nome' })}
          
        />
        <s.Pagr visible ={errors.name ? 1 : 0}>
          {errors.name && errors.name.message }
        </s.Pagr>

        <span>Nome de usuário</span>
        <input
          type="text"
          name="userName"
          ref={register({ required: 'preencha o nome de usuário' })}
        />
        <s.Pagr visible ={errors.userName ? 1 : 0}>
          {errors.userName && errors.userName.message}
        </s.Pagr>

        <span>Senha</span>
        <input
          type="text"
          name="password"
          ref={register({ required: 'preencha a senha' })}
        />
        <s.Pagr visible ={errors.password ? 1 : 0}>
          {errors.password && errors.password.message}
        </s.Pagr>

        <span>Confirme sua senha</span>
        <input
          type="text"
          name="passwordConfirm"
          ref={register({ required: 'confirme a senha' })}
        />
        <s.Pagr visible ={errors.passwordConfirm ? 1 : 0}>
          {errors.passwordConfirm && errors.passwordConfirm.message}
        </s.Pagr>

        <button type="submit">ENVIAR</button>
      </form>
    </s.Container>
  );
}

export default Form;
