import React, { useState, useEffect } from "react";
import ReactLoading from "react-loading";
import { toast } from "react-toastify";
import api from "../../services/api";
import * as s from "./styles";
import EditingName from "../EditClients/EditingName/index";
import EditingContact from "../EditClients/EditingContact/index";
import EditingEmail from "../EditClients/EditingEmail/index";
import EditingAddress from "../EditClients/EditingAddress/index";
import icomTrashCan from "../../assests/image/icons/trash-can.png";


function CustomerInfo() {
  const [datas, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [id, setId] = useState({
    id: "",
  }); // id, name, address, contact, email

  async function deleteClient(e) {
    try {
      setLoading(true)
     await  api.delete(`client/delete/${e}`);
      const identification = datas.filter(
        (client) => client.identificador !== e
      );
      // o filter pecorrer o datas e retorna, em array, os dados que passaram no teste
      setData(identification);
      toast.success("cliente excluído com sucesso");
      console.log(identification);
    } catch (err) {
      toast.error(`error: ${err}`);
      
    }
    setLoading(false)
  }
  function edit(idD) {
    setId({
      id: idD,
    });
  }
  function showName(idForName) {
    /* como todas as outras funções com o prefixo "show", a função irá receber
     o id do input, enviar para o componente correspondente ao nome do id. */
    const element = document.querySelector(".name");
    element.classList.toggle("hide");
    edit(idForName);
  }
  function showAddress(idForAddress) {
    const element = document.querySelector(".address");
    element.classList.toggle("hide");
    edit(idForAddress);
  }

  function showemail(idForEmail) {
    const element = document.querySelector(".email");
    element.classList.toggle("hide");
    edit(idForEmail);
  }

  async function getList() {
    setLoading(true);
    try {
      const { data } = await api.get("/client/findall");
      const { clientes } = data;
      setData([...clientes]);
    } catch (error) {
      console.error("error :>> ", error);
    }
    setLoading(false);
  }
  return (
    <>
      {loading ? (
        <s.Loading>
          <ReactLoading type="balls" width="64px" height="64px" color="#000" />
        </s.Loading>
      ) : (
        <s.Container>
          <s.Content>
            <h1>Visualizar clientes</h1>
           <s.ButtonWrapper>
              <button type='button'onClick={()=> getList()}>
                <img src="https://img.icons8.com/ios-filled/50/000000/list.png"/>
                carrega todos os clientes
                </button>
              <button type='button'>
              <img src="https://img.icons8.com/material-rounded/48/000000/search-folder.png"/>
                pesuisar por identificador
                </button>
           </s.ButtonWrapper>
           {datas.length > 0 && ( <table>
              <thead>
                <tr>
                  <th>
                    <strong>NOME</strong>
                  </th>
                  <th>
                    <strong>NOME DE USUÁRIO</strong>
                  </th>

                  <th>
                    <strong>ATUALIZAÇÃO</strong>
                  </th>
                </tr>
              </thead>
              <tbody>
                {datas.map((client) => (
                  <tr key={client.identificador}>
                    <s.TableData title="click to edit">
                      <button
                        type="button"
                        onClick={() => showName(client.identificador)}
                      >
                        {client.nome}
                      </button>
                    </s.TableData>
                    <s.TableData title="click to edit">
                      <button
                        type="button"
                        onClick={() => showAddress(client.identificador)}
                      >
                        {" "}
                        {client.nomeDeUsuario}
                      </button>
                    </s.TableData>
                    <s.TableData title="click to edit">
                      <button
                        type="button"
                        onClick={() => showemail(client.identificador)}
                      >
                        {client.ultimoAcesso}
                      </button>
                    </s.TableData>
                    <s.EditTable>
                      <button
                        type="button"
                        onClick={() => deleteClient(client.identificador)}
                      >
                        <img
                          src={icomTrashCan}
                          alt="deletar usuário"
                          title="deletar usuário"
                        />
                      </button>
                    </s.EditTable>
                  </tr>
                ))}
              </tbody>
            </table>)}

            <s.InvisibleComponentName>
              <div className="name hide">
                <EditingName id={id} />
              </div>
            </s.InvisibleComponentName>
            <s.InvisibleComponentAddress>
              <div className="address hide">
                <EditingAddress id={id} />
              </div>
            </s.InvisibleComponentAddress>
            <s.InvisibleComponentContact>
              <div className="contact hide">
                <EditingContact id={id} />
              </div>
            </s.InvisibleComponentContact>
            <s.InvisibleComponentEmail>
              <div className="email hide">
                <EditingEmail id={id} />
              </div>
            </s.InvisibleComponentEmail>
          </s.Content>
        </s.Container>
      )}
    </>
  );
}

export default CustomerInfo;
